.content-activation {
  padding-bottom: pxToRem(76px);
  .factory_model{
      width:100%;
      background: url("../images/activation/factory_model.png") center no-repeat;
      height: pxToRem(240px);
      background-size: cover;
  }
  .production_model{
    width:100%;
    background: url("../images/activation/production_model.png") center no-repeat;
    height: pxToRem(240px);
    background-size: cover;
  }
  .logo2 {
    margin: 0 auto;
  }
  .form-active {
    margin-top: pxToRem(20px);
    .item-wrapper {
      position: relative;
      // padding-left: pxToRem(30px);
      // height: pxToRem(98px);

      // font-size: pxToRem(32px);
      // line-height: pxToRem(98px);

      // color: #333;
      // background-color: #fff;
      input,
      select,
      p {
        display: inline-block;
        float: right;
        height: 100%;
        box-sizing: border-box;
        padding-right: pxToRem(58px);
        width: auto;
        max-width: pxToRem(580px);
        text-align: right;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        z-index: 9999;
        &.warn{
            color: red;
        }
      }
      &:after {
        // @include border(bottom, #e5e5e5);

        left: pxToRem(30px);
      }
      .scan{
           display: block;
           position: absolute;
           width: pxToRem(60px);
           height: pxToRem(60px);
           background: url("../images/activation/icon_scan.png");
           background-size: contain;
           top:pxToRem(20px);
           right:pxToRem(20px);
      }
      &.scan_code{
          white-space: nowrap;
          input{
              padding-right: pxToRem(90px);
              width: auto;
          }
      }
      label {
        padding: 0;
        width: pxToRem(130px);
        text-align: left;
      }
      select {
        border: 0;
        height: 100%; // padding: 0;

        font-size: pxToRem(32px);

        background-color: transparent;

        outline: none;

        direction: rtl;
      }
      i {
        margin-right: pxToRem(10px);

        vertical-align: text-top;
      }
      &.item-large {
        height: auto;
        line-height: normal;
      }
      &.checkboxs {
        padding-bottom: pxToRem(14px);
        .checkbox-wrap {
          // position: relative;
          // top: 30%;

          // width: 90%;
          // height: 40%;

          // background-color: white;
          font-size: 0;
          vertical-align: middle;
        }
        fieldset legend {
            padding: pxToRem(34px) 0 pxToRem(14px);
        }
        input[type="checkbox"] {
          display: none;
        }
        input[type="checkbox"] + label {
          display: inline-block;

          // margin-top: 10px;
          // margin-left: 5px;
          margin: pxToRem(20px) 0;
          -webkit-box-sizing: border-box;
          width: 33.33%;

          text-align: left;
          font-size: pxToRem(32px);
        }

        label::before {
          display: inline-block;

          @include inline-background("data:img/jpg;base64,iVBORw0KGgoAAAANSUhEUgAAACIAAAAiBAMAAADIaRbxAAAAHlBMVEUAAADMzMzMzMzMzMzMzMzMzMzMzMzMzMzMzMzMzMwLbf+mAAAACXRSTlMA8q0RbifpdiWvaVS1AAAAOElEQVQoz2PgUJyJDIQaGMKcGZCBSSqDCgMqKGIQRhMxZJiAJsI5KjLkRTBjGTMlYKYWVrQUFQAALl0kcAxXPqkAAAAASUVORK5CYII=", 34px, 34px);

          vertical-align: sub;

          content: "";
          margin-right: pxToRem(12px);
        }
        input[type="checkbox"]:checked + label::before {

          @include inline-background("data:img/jpg;base64,", 34px, 34px);
        }
      }
    }
    .btn {
      margin-top: pxToRem(40px);
    }
    .status-non {
      color: #ccc;
      .icon-loca {
        background-position: pxToRem(0px) pxToRem(-51px);
      }
    }
    .status-fail {
      color: #f0534b;
      .icon-loca {
        width: pxToRem(28px);

        background-position: pxToRem(-34px) pxToRem(-51px);
      }
    }
    .not-select {
      color: #999;
    }
  }
}

