.popup-location {
  display: none;

  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  background-color: #fff;
  .close {
    position: absolute;
    top: pxToRem(30px);
    left: pxToRem(30px);
    z-index: 2000;

    border-radius: pxToRem(80px);
    width: pxToRem(80px);
    height: pxToRem(80px);

    background: url("data:img/jpg;base64,") no-repeat center;
    background-color: rgba(0, 0, 0, .55);
    background-size: pxToRem(34px) pxToRem(34px);
  }
  .confirm_postion{
      position: absolute;
      height: pxToRem(80px);
      line-height: pxToRem(80px);
      width: 100%;
      bottom: 0;
      left: 0;
      text-align: center;
      color: #fff;
      background-color: #007aff;
      z-index: 999;
      font-size: pxToRem(30px);
  }
  iframe {
    position: relative;

    width: 100vw;
    height: 100vh;
  }
}


