// 雪碧图
.sprite {
  display: block;

  background-image: url(../images/activation/sprite.png);
  background-repeat: no-repeat;
  background-size: pxToRem(106px)  pxToRem(85px);
}
.icon-loca {
  @extend .sprite;

  display: inline-block;

  width: pxToRem(26px);
  height: pxToRem(34px);

  background-position: pxToRem(-78px) pxToRem(0px);
}
.icon-loca2 {
  @extend .sprite;

  width: pxToRem(26px);
  height: pxToRem(34px);

  background-position: pxToRem(0px) pxToRem(-51px);
}
.icon-loca3 {
  @extend .sprite;

  width: pxToRem(26px);
  height: pxToRem(34px);

  background-position: pxToRem(-36px) pxToRem(-51px);
}
.icon-lock {
  @extend .sprite;

  display: inline-block;

  width: pxToRem(30px);
  height: pxToRem(41px);

  background-position: pxToRem(0px) pxToRem(0px);
}
.icon-phone {
  @extend .sprite;

  display: inline-block;

  width: pxToRem(30px);
  height: pxToRem(40px);

  background-position: pxToRem(-40px) pxToRem(0px);
}
